<template>
  <div class="box">
    <LayoutBox one="店铺审核">
      <div class="LayoutBox">
        <div class="title">
          <p>商家详情</p>
          <el-divider></el-divider>
        </div>
        <el-form :model="shopData" label-width="100px" size="mini">
          <el-form-item label="商铺名称:">
            <span>{{ shopData.shopName }}</span>
          </el-form-item>
          <el-form-item label="联系人:">
            <span>{{ shopData.name }}</span>
          </el-form-item>
          <el-form-item label="联系人电话:">
            <span>{{ shopData.mobile }}</span>
          </el-form-item>
          <el-form-item label="地区:">
            <span>{{ shopData.area }}</span>
          </el-form-item>
          <el-form-item label="详细地址:">
            <span>{{ shopData.adderss }}</span>
          </el-form-item>
          <el-form-item label="税号:">
            <span>{{ shopData.tax }}</span>
          </el-form-item>
          <el-form-item label="身份证:">
            <div class="identityCardUrl_group">
              <el-image v-for="(item, i) in shopData.identityCardUrl" :key="i" class="identityCardUrl" :src="item" fit="contain"></el-image>
            </div>
          </el-form-item>
          <el-form-item label="营业执照:">
            <el-image class="businessLicenseUrl" :src="shopData.businessLicenseUrl" fit="contain"></el-image>
          </el-form-item>
          <el-form-item label="卫生许可证:">
            <el-image class="sanitaryOperationLicenseUrl" :src="shopData.sanitaryOperationLicenseUrl" fit="contain"></el-image>
          </el-form-item>
          <el-form-item label="门头照:">
            <div class="doorHeadUrl_group">
              <el-image v-for="(item, i) in shopData.doorHeadUrl" :key="i" class="doorHeadUrl" :src="item" fit="contain"></el-image>
            </div>
          </el-form-item>
        </el-form>
      </div>
    </LayoutBox>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'

export default {
  name: 'SuperMarketAddDetail',

  components: {
    LayoutBox
  },

  data () {
    return {
      shopData: { // 商铺信息
        shopName: '',
        name: '',
        mobile: '',
        area: '',
        adderss: '',
        tax: '',
        identityCardUrl: [],
        businessLicenseUrl: '',
        sanitaryOperationLicenseUrl: '',
        doorHeadUrl: ''
      },
      id: '' // 店铺主键id
    }
  },

  created () {
    this.id = this.$route.query.id // 店铺主键id
    this._findShopData()
  },

  mounted () {

  },

  methods: {
    // api 接口-------------------------------------------------------
    // 获取店铺详细
    _findShopData () {
      this.$http.post(`/woho/shop/manage/findById/${this.id}`)
        .then((res) => {
          console.log(res)
          this.shopData = res.data
          this.shopData.identityCardUrl = res.data.identityCardUrl.split(',')
          this.shopData.doorHeadUrl = res.data.doorHeadUrl.split(',')
        })
    }
    // api 接口-------------------------------------------------------
  }
}
</script>

<style lang="scss" scoped>
  .box{
    min-width: 1100px;
    .LayoutBox{
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .title{
        text-align: center;
        width: 583px;
        font-size: 22px;
      }
      .identityCardUrl_group{
        display: flex;
        flex-direction: column;
        .identityCardUrl{
          width: 171px;
          height: 108px;
        }
      }
      .businessLicenseUrl{
        width: 163px;
        height: 231px;
      }
      .sanitaryOperationLicenseUrl{
        width: 246px;
        height: 172px;
      }
      .doorHeadUrl_group{
        display: flex;
        flex-direction: column;
        .doorHeadUrl{
          width: 328px;
          height: 185px;
        }
      }
      .btn_group{
        display: flex;
      }
    }
  }
</style>
